.s-embed {
  position: relative;
  overflow: hidden;
  height: 0;
  max-width: 100%;
  padding-bottom: 75%; /* 4:3 */

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
}

.s-embed--widescreen {
  padding-bottom: 56.25%; /* 16:9 */
}
